.accordion {
    &__row{
        border-top: 1px solid rgba($borderColor, .2);
        border-bottom: 1px solid rgba($borderColor, .2);
        padding: 15px 10px 15px 0;
        
    }
    &__tab-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        cursor: pointer;
        &--title{
            @include font-size(14);
            font-family: $fontSemiBold;
            color: $black;
        }
    }
    &__icon {
        position: relative;
        top: 5px;
    }

    &__tab-container {
       display: none;
       padding-top:10px;

       &.open{
           display: block;
       }
    }
}